import React from "react";
import { connect } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown, faChevronUp } from "@fortawesome/free-solid-svg-icons";
import {
  getTime,
  formatDuration,
  formatPrice,
  displayPrice,
  getLabel
} from "../../../../utils/functions";
import { updateFlightsFilter } from "../../../../redux/actions";
import Checkbox from "../../../Searchbox/_commons/Checkbox/index.jsx";
// import Radio from "../../../Searchbox/_commons/Radio/index.jsx";
import Slider from "../../../Searchbox/_commons/Slider/index.jsx";
import cx from "classnames";
require("./style.scss");

const airlinesLimit = Number.POSITIVE_INFINITY; //10;

const titles = {
  stops: getLabel("stops"),
  airports: getLabel("airports"),
  duration: getLabel("travel.duration"),
  departure: getLabel("departure.times"),
  arrival: getLabel("arrival.times"),
  airlines: getLabel("airlines"),
  price: getLabel("price")
};

const options = {
  stops: {
    zero: getLabel("direct"),
    one: getLabel("one.stop"),
    more: getLabel("more.stops")
  }
};

const onSliderChange = (updateFilterValue, field, edge, value, param) => {
  if (field === "departure" || field === "arrival") {
    updateFilterValue(field + value, { min: param[0], max: param[1] });
  } else if (field === "stops") {
    updateFilterValue(field + value, param);
  } else if (field === "airlines") {
    updateFilterValue(field, param, value);
  } else {
    updateFilterValue(field, value);
  }
};

const getContent = (
  type,
  edge,
  value,
  updateFilterValue,
  state,
  showAllAirlines
) => {
  if (!edge || !value) return null;
  const content = [];
  switch (type) {
    case "stops":
      for (let item in options[type]) {
        if (edge[item] !== 999999) {
          content.push(
            <Checkbox
              key={item}
              checked={value[item]}
              onCheckboxChange={onSliderChange.bind(
                this,
                updateFilterValue,
                type,
                edge,
                item
              )}
            >
              {options[type][item]}
              <span className="price">
                {getLabel("price.from")} {displayPrice(formatPrice(edge[item]))}
              </span>
            </Checkbox>
          );
        }
      }
      break;
    case "airports":
      break;
    case "airlines":
      Object.keys(edge)
        .sort((a, b) => {
          if (edge[a].name > edge[b].name) return 1;
          if (edge[a].name < edge[b].name) return -1;
          return 0;
        })
        .forEach(key => {
          if (typeof value[key] === "boolean") {
            if (
              state.allAirlines ||
              (!state.allAirlines && content.length < airlinesLimit)
            ) {
              content.push(
                <Checkbox
                  key={key}
                  checked={value[key]}
                  onCheckboxChange={onSliderChange.bind(
                    this,
                    updateFilterValue,
                    type,
                    edge,
                    key
                  )}
                >
                  {edge[key].name}
                  <span className="price">
                    {getLabel("price.from")}{" "}
                    {displayPrice(formatPrice(edge[key].price))}
                  </span>
                </Checkbox>
              );
            }
          }
        });
      if (Object.keys(edge).length > airlinesLimit) {
        content.push(
          <div key="showAll" className="showAll">
            <span onClick={showAllAirlines}>
              {getLabel(
                state.allAirlines ? "show.less.airlines" : "show.all.airlines"
              )}
            </span>
          </div>
        );
      }
      break;
    case "price":
    case "duration":
      let txt = "";
      let step = 1;
      if (type === "price") {
        txt = displayPrice(value);
      }
      if (type === "duration") {
        txt = formatDuration(value);
        step = 5;
      }
      content.push(
        <div key="sliderLabel" className="sliderLabel-center">
          <span>max. {txt}</span>
        </div>
      );
      content.push(
        <Slider
          key="slider"
          min={edge.min}
          max={edge.max}
          value={value}
          step={step}
          onChange={onSliderChange.bind(this, updateFilterValue, type, edge)}
        />
      );
      break;
    case "departure":
    case "arrival":
      content.push(
        <div key="outboundLabel" className="sliderLabel">
          {getLabel("outbound")}
          <span>
            {getTime(value.outbound.min, "dd HH:mm", "X")}
            {" - "}
            {getTime(value.outbound.max, "dd HH:mm", "X")}
          </span>
        </div>
      );
      content.push(
        <Slider
          key="outboundSlider"
          range
          allowCross={false}
          min={edge.outbound.min}
          max={edge.outbound.max}
          value={[value.outbound.min, value.outbound.max]}
          step={300}
          onChange={onSliderChange.bind(
            this,
            updateFilterValue,
            type,
            edge,
            "Out"
          )}
        />
      );
      if (value.inbound.min && value.inbound.max) {
        content.push(
          <div key="inboundLabel" className="sliderLabel">
            {getLabel("inbound")}
            <span>
              {getTime(value.inbound.min, "dd HH:mm", "X")}
              {" - "}
              {getTime(value.inbound.max, "dd HH:mm", "X")}
            </span>
          </div>
        );
        content.push(
          <Slider
            key="inboundSlider"
            range
            allowCross={false}
            min={edge.inbound.min}
            max={edge.inbound.max}
            value={[value.inbound.min, value.inbound.max]}
            step={300}
            onChange={onSliderChange.bind(
              this,
              updateFilterValue,
              type,
              edge,
              "In"
            )}
          />
        );
      }
      break;
    default:
  }
  return content;
};

class FilterBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: !props.closed,
      allAirlines: false
    };
  }

  toggleVisible = () => {
    this.setState({ visible: !this.state.visible });
  };

  toggleAllAirlines = () => {
    this.setState({ allAirlines: !this.state.allAirlines });
  };

  render() {
    const {
      type,
      // closed,
      edges,
      filter,
      updateFilterValue
    } = this.props;
    let edge = edges[type];
    let value = filter[type];
    if (type === "departure" || type === "arrival") {
      value = {
        outbound: filter[type + "Out"],
        inbound: filter[type + "In"]
      };
    } else if (type === "stops") {
      value = {
        zero: filter.stopszero,
        one: filter.stopsone,
        more: filter.stopsmore
      };
    }
    return (
      <div className={cx("filterBox", { visible: this.state.visible })}>
        <div className="title" onClick={this.toggleVisible}>
          {titles[type].charAt(0).toUpperCase() +
            titles[type].slice(1).toLowerCase()}
          <FontAwesomeIcon
            icon={this.state.visible ? faChevronDown : faChevronUp}
            className="icon"
          />
        </div>
        {this.state.visible &&
          getContent(
            type,
            edge,
            value,
            updateFilterValue,
            this.state,
            this.toggleAllAirlines
          )}
      </div>
    );
  }
}

export default connect(
  (state, ownProps) => ({
    filter: state.flights.filter.values,
    edges: state.flights.filter.edges
  }),
  (dispatch, ownProps) => ({
    updateFilterValue: (field, value, param = null) =>
      dispatch(updateFlightsFilter(field, value, param))
  })
)(FilterBox);
