const ResultsPanel = ({ results, criteria, responsesCounter }) => {
  const { departure = { code: "" }, destination = { code: "" } } = criteria;
  return (
    <div className="resultsPanel">
      {renderMeta("results.flights", {
        depAirport: departure.code,
        desAirport: destination.code
      })}
      <div className="resultsPanel__searchSummary">
        <div className="resultsPanel__searchSummary__box">
          <SearchSummary />
        </div>
      </div>
      <div className="resultsPanel__results">
        <div className="resultsPanel__results__box">
          {responsesCounter === 0 ? (
            <ResultsLoader title={`${departure.code} - ${destination.code}`} />
          ) : (
            <>
              <div className="resultsPanel__results__box--filter">
                <FilterPanel />
              </div>
              <div className="resultsPanel__results__box--content">
                <FlightResults />
                <PagingBar />
              </div>
            </>
          )}
          {/*<div className="resultsPanel__results__box--filter">
            <FilterPanel />
          </div>
          <div className="resultsPanel__results__box--content">
            {getTitle(responsesCounter, results.length)}
            {results.length > 0 && (
              <div>
                <SortingBar />
                {/*<FilterPanel />* /}
                <FlightResults />
                <PagingBar />
              </div>
            )}
            </div>*/}
        </div>
      </div>
    </div>
  );
};

export default connect((state, ownProps) => ({
  criteria: state.flights.criteria,
  results: state.flights.results,
  responsesCounter: state.flights.responsesCounter
}))(ResultsPanel);
