import React from "react";
import { connect } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import Modal from "../../../_commons/Modal";
// import Balloon from "../../../_commons/Balloon/index.jsx";
import BoundDetails from "../BoundDetails/index.jsx";
import FlightOverview from "../FlightOverview/index.jsx";
import {
  // getTime,
  // formatDuration,
  // formatPrice,
  getLabel,
  displayPrice
} from "../../../../utils/functions";
import { toggle } from "../../../../redux/actions";
// import cx from "classnames";
require("./style.scss");

const storeFare = fare => () => {
  if (typeof Storage === "undefined") return;
  sessionStorage[`fare-${fare.key}`] = JSON.stringify(fare);
};

const Deeplink = ({ shop, fare, children }) => (
  <a
    href={`#/redirect/${shop.code}/${fare.key}?url=${window.encodeURIComponent(
      shop.url
    )}`}
    target="_blank"
    rel="noopener noreferrer"
    onClick={storeFare(fare)}
  >
    {children}
  </a>
);

class FlightResult extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: {
        details: false
      }
    };
  }

  toggleVisible = what => () => {
    const { state } = this;
    state.visible[what] = !state.visible[what];
    this.setState(state);
  };

  render() {
    const {
      fare
      // isVisible,
      // onToggle
    } = this.props;
    const {
      // price,
      // marketingCarrier,
      // isOneway,
      outbounds,
      inbounds,
      durations,
      // key,
      // url,
      shops,
      cabinClass
    } = fare;
    const departure = outbounds[0];
    const destination = outbounds[outbounds.length - 1];
    console.log({ departure });
    return (
      <>
        <div className="flightResult" onClick={this.toggleVisible("details")}>
          <FlightOverview {...fare} />
          <div className="bottomBar">
            <div className="bottomBarBtns">
              <div>
                {/*<Deeplink shop={shops[0]} fare={fare}>*/}
                <button>
                  {getLabel("book.from")}
                  <strong>{displayPrice(fare.price)}</strong>
                </button>
                <div className="cheapestShop">
                  {getLabel("with")} {shops[0].name}
                </div>
              </div>
              <div>
                {shops
                  .filter((_, idx) => idx > 0)
                  .map((shop, index) => (
                    <div key={index} className="shop">
                      <span className="name">{shop.name}</span>
                      <span className="price">{displayPrice(shop.price)}</span>
                    </div>
                  ))}
              </div>
              {/*</Deeplink>*/}
              {/*<div
                className={cx("btn", {
                  btnHidden: this.state.visible.details
                })}
              >
                {shops.length === 1 && (
                  <Deeplink shop={shops[0]} fare={fare}>
                    <button>{getLabel("choose")}</button>
                  </Deeplink>
                )}
                {shops.length > 1 && (
                  <button
                    className={cx("moreShops", { visible: isVisible[key] })}
                    onClick={onToggle.bind(this, key)}
                  >
                    {getLabel("choose")}
                  </button>
                )}
                {isVisible[key] && (
                  <Balloon>
                    {shops.map((shop, index) => (
                      <div key={index} className="shop">
                        <Deeplink shop={shop} fare={fare}>
                          <span className="price">
                            {displayPrice(shop.price)}
                          </span>
                          <span className="name">{shop.name}</span>
                          <button>{getLabel("choose")}</button>
                        </Deeplink>
                      </div>
                    ))}
                  </Balloon>
                )}
                    </div>*/}
            </div>
            {/*this.state.visible.details && (
            <div className="tabDetails">
              <div>
                <div className="title">{getLabel("details")}</div>
                <div className="close" onClick={this.toggleVisible("details")}>
                  {getLabel("close.details")}
                </div>
              </div>
              <div className="content">
                <div className="shops">
                  {shops.map((shop, index) => (
                    <div key={index} className="shopBtn">
                      <Deeplink shop={shop} fare={fare}>
                        <div className="shop">
                          <div className="btn">
                            <span className="choose">
                              {getLabel("book.now")}
                            </span>
                            <span className="price">
                              {displayPrice(shop.price)}
                            </span>
                            <span className="name">{shop.name}</span>
                          </div>
                        </div>
                      </Deeplink>
                    </div>
                  ))}
                </div>
                <BoundDetails
                  type="outbound"
                  bounds={outbounds}
                  duration={durations.outbound}
                  cabinClass={cabinClass}
                />
                <BoundDetails
                  type="inbound"
                  bounds={inbounds}
                  duration={durations.inbound}
                  cabinClass={cabinClass}
                />
                {/*}<button>Share</button>* /}
              </div>
            </div>
          )*/}
          </div>
        </div>
        <Modal
          isOpen={this.state.visible.details}
          // onAfterOpen={afterOpenModal}
          onRequestClose={this.toggleVisible("details")}
          // style={customStyles}
          contentLabel="Fare details"
        >
          <div className="content">
            <div className="head">
              <FontAwesomeIcon
                icon={faTimes}
                className="close"
                onClick={this.toggleVisible("details")}
              />
              {departure.departureAirportCode} &gt;{" "}
              {destination.arrivalAirportCode}
            </div>
            <div className="shops">
              {shops.map((shop, index) => (
                <div key={index} className="shopBtn">
                  <div className="shop">
                    <span className="name">{shop.name}</span>
                    <div>
                      <span className="price">{displayPrice(shop.price)}</span>
                      <Deeplink shop={shop} fare={fare}>
                        <button className="choose">
                          {getLabel("book.now")}
                        </button>
                      </Deeplink>
                    </div>
                  </div>
                </div>
              ))}
            </div>
            <div className="bounds">
              <BoundDetails
                type="outbound"
                bounds={outbounds}
                duration={durations.outbound}
                cabinClass={cabinClass}
              />
              <BoundDetails
                type="inbound"
                bounds={inbounds}
                duration={durations.inbound}
                cabinClass={cabinClass}
              />
              {/*}<button>Share</button>*/}
            </div>
          </div>
        </Modal>
      </>
    );
  }
}

export default connect(
  (state, ownProps) => ({
    isVisible: state.toggles.flightsResultsPageShopList
  }),
  (dispatch, ownProps) => ({
    onToggle: (item, event) => {
      event.stopPropagation();
      dispatch(toggle("flightsResultsPageShopList", item));
    }
  })
)(FlightResult);
