import React from "react";
require("./style.scss");

export default ({ checked, onCheckboxChange, children }) => {
  let checkbox;
  const id = "checkbox-" + Math.random();
  return (
    <div className="checkbox">
      <input
        type="checkbox"
        id={id}
        ref={node => {
          checkbox = node;
        }}
        checked={checked}
        onChange={() => onCheckboxChange(checkbox.checked)}
      />
      <label htmlFor={id}>{children}</label>
    </div>
  );
};
