import React from "react";
import { connect } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faCaretUp } from "@fortawesome/free-solid-svg-icons";
import Balloon from "../../../_commons/Balloon/index.jsx";
import TravellersOption from "./TravellersOption/index.jsx";
import ComboboxCabinClass from "../ComboboxCabinClass";
import { toggle, crementTraveller } from "../../../../redux/actions";
import { getLabel } from "../../../../utils/functions";
import cx from "classnames";
require("./style.scss");

const types = {
  adt: {
    name: getLabel("adults"),
    age: "18+"
  },
  ch: {
    name: getLabel("youngs"),
    age: "12-17"
  },
  inf: {
    name: getLabel("kids"),
    age: "2-11"
  }
};

// const getIcon = (value, type) => (
//   <span key={type} className="iconized">
//     {value}
//     <img
//       src={require("../../../../static/images/icons/" + type + ".png")}
//       alt={type}
//     />
//   </span>
// );

const getValues = ({ adt, ch, inf }) => {
  const travellers = adt + ch + inf;
  return (
    travellers + " " + getLabel(travellers === 1 ? "traveller" : "travellers")
  );
  // if (!ch && !inf) return adt + " " + getLabel("adult" + (adt > 1 ? ".s" : ""));
  // const labels = [];
  // labels.push(getIcon(adt, "adt"));
  // if (ch) labels.push(getIcon(ch, "ch"));
  // if (inf) labels.push(getIcon(inf, "inf"));
  // return labels;
};

const getAmount = ({ adt, ch, inf }) => {
  if (!ch && !inf) return 1;
  if (!ch || !inf) return 2;
  return 3;
};

const ComboboxTravellers = ({
  isVisible,
  travellers,
  cabinClass,
  onToggle,
  crementTraveller
}) => (
  <div className="comboboxTravellers">
    <div className={cx("combobox", { opened: isVisible })} onClick={onToggle}>
      <div className={cx("visible", "amount--" + getAmount(travellers))}>
        {getValues(travellers)}, {getLabel("class." + cabinClass.toLowerCase())}
      </div>
      <FontAwesomeIcon
        icon={isVisible ? faCaretUp : faCaretDown}
        className="icon"
      />
    </div>
    {isVisible && (
      <Balloon>
        {Object.keys(types).map(type => (
          <TravellersOption
            key={type}
            type={type}
            {...types[type]}
            onUpdate={crementTraveller}
            value={travellers[type]}
          />
        ))}
        <ComboboxCabinClass />
      </Balloon>
    )}
  </div>
);

export default connect(
  (state, ownProps) => ({
    isVisible: state.toggles.flightsSearchboxComboboxTravellers,
    travellers: state.flights.searchbox.travellers,
    cabinClass: state.flights.searchbox.cabinClass.code
  }),
  (dispatch, ownProps) => ({
    onToggle: event => {
      event.stopPropagation();
      dispatch(toggle("flightsSearchboxComboboxTravellers"));
    },
    crementTraveller: (traveller, value, event) => {
      event.stopPropagation();
      dispatch(crementTraveller(traveller, value));
    }
  })
)(ComboboxTravellers);
