import React from "react";
import {
  getTime,
  formatDuration
  // formatPrice,
  // displayPrice,
  // getLabel
} from "../../../../utils/functions";
require("./style.scss");

// const getStopsText = stops =>
//   stops + " " + getLabel(stops === 1 ? "stop" : "stop.s");

const segmentDetails = (bounds, duration) => {
  if (!bounds.length) return null;
  const departure = bounds[0];
  const destination = bounds[bounds.length - 1];
  const stops = bounds.length - 1;
  const { code, name } = bounds[0].airlineOperating;
  return (
    <div className="segment">
      <span className="airline">
        <img src={`/airlines/${code}.png`} alt={name} />
        <div>{name}</div>
      </span>
      <span className="takeoff">
        <span className="hour">{getTime(departure.dep.time)}</span>
        <span className="airport">{departure.dep.code}</span>
      </span>
      <span className="arrow-stops">
        <span className="arrow-stops-in">
          {stops > 0 ? <small>{stops}</small> : ""}
        </span>
      </span>
      <span className="takeoff destination">
        <span className="hour">{getTime(destination.arr.time)}</span>
        <span className="airport">{destination.arr.code}</span>
      </span>
      <span className="duration">{formatDuration(duration)}</span>
      {/*<span className="stops">
        {bounds.length - 1
          ? getStopsText(bounds.length - 1)
          : getLabel("direct")}
        </span>*/}
    </div>
  );
};

export default ({
  price,
  marketingCarrier,
  outbounds,
  inbounds,
  durations
}) => (
  <div className="fare">
    {/*<div className="general">
      <div className="price">{displayPrice(formatPrice(price))}</div>
      <div className="airline">{marketingCarrier.name}</div>
      <div className="airlineLogo">
        <img
          src={require(`../../../../static/images/airlines/${marketingCarrier.code}.png`)}
          alt={marketingCarrier.name}
        />
      </div>
    </div>*/}
    <div className="details">
      <div className="segments">
        {segmentDetails(outbounds, durations.outbound)}
        {segmentDetails(inbounds, durations.inbound)}
      </div>
    </div>
  </div>
);
