import React from "react";
import { connect } from "react-redux";
import {
  formatDate,
  formatTravellers,
  getLabel
} from "../../../../utils/functions";
import ResultsLoaderBar from "../../_commons/ResultsLoaderBar/index.jsx";
import Searchbox from "../../../Searchbox/Flights/index.jsx";
import cx from "classnames";
require("./style.scss");

const dateFormat = "DD/MM";

class SearchSummary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isSearchbox: true
    };
    this.onToggleSearchbox = this.onToggleSearchbox.bind(this);
  }

  onToggleSearchbox() {
    // this.setState({ isSearchbox: !this.state.isSearchbox });
  }

  componentWillReceiveProps() {
    // if (this.props.counter > 0) this.setState({ isSearchbox: false });
  }

  render() {
    const { searchbox } = this.props;
    if (!Object.keys(searchbox).length) return <div />;
    return (
      <div>
        {!this.state.isSearchbox && (
          <div className="searchSummary">
            <div className="airports">
              <span>{searchbox.departure.code}</span>
              <span className={cx("arrow", "arrow--right")}>
                &nbsp;
                {!searchbox.isOneway && (
                  <span className="arrow--left">&nbsp;</span>
                )}
              </span>
              <span>{searchbox.destination.code}</span>
              <span className="direction">
                {getLabel(searchbox.isOneway ? "one.way" : "return")}
              </span>
            </div>
            <div className="dates">
              <div>
                <span>{formatDate(searchbox.departureDate, "dddd")}</span>
                <span>{formatDate(searchbox.departureDate, dateFormat)}</span>
                <span>{formatDate(searchbox.departureDate, "DD MMM")}</span>
              </div>
              {!searchbox.isOneway && (
                <div className="arrow--right">&nbsp;</div>
              )}
              {!searchbox.isOneway && (
                <div>
                  <span>{formatDate(searchbox.returnDate, "dddd")}</span>
                  <span>{formatDate(searchbox.returnDate, dateFormat)}</span>
                  <span>
                    &nbsp;-&nbsp;{formatDate(searchbox.returnDate, "DD MMM")}
                  </span>
                </div>
              )}
            </div>
            <div className="other">
              <span>
                {formatTravellers(searchbox.travellers)}
                {searchbox.travellers.ch === 0 && searchbox.travellers.inf === 0
                  ? ", "
                  : ""}
              </span>
              <span>
                {getLabel(`class.${searchbox.cabinClass.code.toLowerCase()}`)}
              </span>
            </div>
            <button onClick={this.onToggleSearchbox}>
              {getLabel("change")}
            </button>
            <ResultsLoaderBar />
          </div>
        )}
        {this.state.isSearchbox && (
          <div className="searchbox">
            <Searchbox
            // narrow
            />
            {/*<div className="close" onClick={this.onToggleSearchbox}></div>*/}
          </div>
        )}
      </div>
    );
  }
}

export default connect((state, ownProps) => ({
  searchbox: state.flights.criteria,
  counter: state.flights.responsesCounter
}))(SearchSummary);
