import React from "react";
require("./style.scss");

class Balloon extends React.Component {
  constructor(props) {
    super(props);
    this.ref = null;
  }

  scrollTop = () => {
    this.ref.scrollTop = 0;
    this.ref.scrollLeft = 0;
  };

  render() {
    return (
      <div className="balloon">
        <div
          ref={r => {
            this.ref = r;
          }}
          className="in"
        >
          {this.props.children}
        </div>
      </div>
    );
  }
}

export default Balloon;
