import React, { Component } from "react";
import { connect } from "react-redux";
import { updateFlightsFilter } from "../../../../redux/actions";
import FilterBox from "../FilterBox/index.jsx";
import { getLabel, resetFlightsFilter } from "../../../../utils/functions";
import cx from "classnames";
require("./style.scss");

class FilterPanel extends Component {
  handleReset = () => {
    const { updateFilterValue, edges, isOneway } = this.props;
    resetFlightsFilter(updateFilterValue, edges, isOneway);
  };

  render() {
    const { results, visible } = this.props;
    if (results.length === 0) return null;
    return (
      <div className={cx("filterPanel", { visible })}>
        {/*<div className="filterTitle">
          <h3>{getLabel("filter.your.results")}</h3>
          <div className="resetWrap">
            <div className="link" onClick={this.handleReset}>
              {getLabel("reset.filter")}
            </div>
          </div>
        </div>*/}
        <FilterBox type="price" />
        <FilterBox type="stops" />
        <FilterBox type="airlines" />
        <FilterBox type="duration" />
        <FilterBox type="departure" />
        <FilterBox type="arrival" />
        {/*<FilterBox type="airports" />*/}
        <div className="filterTitle">
          <div className="resetWrap">
            <div className="link" onClick={this.handleReset}>
              {getLabel("reset.filter")}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(
  (state, ownProps) => ({
    results: state.flights.results,
    visible: state.flights.filterVisible,
    edges: state.flights.filter.edges,
    isOneway: state.flights.searchbox.isOneway
  }),
  (dispatch, ownProps) => ({
    updateFilterValue: (field, value, param = null) =>
      dispatch(updateFlightsFilter(field, value, param))
  })
)(FilterPanel);
