import React from "react";
import { connect } from "react-redux";
import {
  toggle,
  setSorting,
  changeFlightsPage,
  toggleFlightsFilterVisible
} from "../../../../redux/actions";
import Balloon from "../../../_commons/Balloon/index.jsx";
import { getLabel } from "../../../../utils/functions";
import cx from "classnames";
require("./style.scss");

const sortOptions = [
  {
    key: "price",
    isAscending: true,
    name: getLabel("price"),
    dir: getLabel("cheap.to.expensive")
  },
  {
    key: "price",
    isAscending: false,
    name: getLabel("price"),
    dir: getLabel("expensive.to.cheap")
  },
  { key: "travelDuration", name: getLabel("travel.duration"), dir: "" },
  {
    key: "airline",
    isAscending: true,
    name: getLabel("airline"),
    dir: "A - Z"
  },
  {
    key: "airline",
    isAscending: false,
    name: getLabel("airline"),
    dir: "Z - A"
  },
  {
    key: "depOutbound",
    isAscending: true,
    name: getLabel("departure.outbound"),
    dir: getLabel("early.to.late")
  },
  {
    key: "depOutbound",
    isAscending: false,
    name: getLabel("departure.outbound"),
    dir: getLabel("late.to.early")
  },
  {
    key: "arrOutbound",
    isAscending: true,
    name: getLabel("arrival.outbound"),
    dir: getLabel("early.to.late")
  },
  {
    key: "arrOutbound",
    isAscending: false,
    name: getLabel("arrival.outbound"),
    dir: getLabel("late.to.early")
  },
  {
    key: "depInbound",
    isAscending: true,
    name: getLabel("departure.inbound"),
    dir: getLabel("early.to.late")
  },
  {
    key: "depInbound",
    isAscending: false,
    name: getLabel("departure.inbound"),
    dir: getLabel("late.to.early")
  },
  {
    key: "arrInbound",
    isAscending: true,
    name: getLabel("arrival.inbound"),
    dir: getLabel("early.to.late")
  },
  {
    key: "arrInbound",
    isAscending: false,
    name: getLabel("arrival.inbound"),
    dir: getLabel("late.to.early")
  }
];

class SortingBar extends React.Component {
  constructor(props) {
    super(props);
    let defaultSortOption;
    const { key, isAscending } = props.sorting;
    sortOptions.forEach(item => {
      if (item.key === key && item.isAscending === isAscending)
        defaultSortOption = item;
    });
    this.state = {
      sorting: defaultSortOption
    };
  }

  handleChooseOption(option) {
    const { state } = this;
    state.sorting = option;
    this.setState(state);
    this.props.onToggle();
    this.props.setSorting({ key: option.key, isAscending: option.isAscending });
  }

  render() {
    return (
      <div className={cx("sortingBar", { visible: this.props.isVisible })}>
        <span className="choose">
          <div className="btn" onClick={this.props.onToggle}>
            <span className="field">
              {getLabel("sorted.by")} {this.state.sorting.name.toLowerCase()}
              {this.state.sorting.dir ? "," : " "}
            </span>
            <span className="order">{this.state.sorting.dir}</span>
          </div>
          <Balloon>
            {sortOptions.map((item, index) => (
              <div
                className="sortOption"
                key={index}
                onClick={this.handleChooseOption.bind(this, item)}
              >
                <strong>{item.name.toLowerCase()}</strong>
                <span>{item.dir}</span>
              </div>
            ))}
          </Balloon>
        </span>
        <div className="btnFilter" onClick={this.props.onFilterToggle}>
          {getLabel("filter")}
        </div>
      </div>
    );
  }
}

export default connect(
  state => ({
    isVisible: state.toggles.flightsResultsPageSortingBar,
    sorting: state.flights.sorting
  }),
  dispatch => ({
    onToggle: event => {
      if (event) event.stopPropagation();
      dispatch(toggle("flightsResultsPageSortingBar"));
    },
    onFilterToggle: () => dispatch(toggleFlightsFilterVisible()),
    setSorting: sorting => {
      dispatch(setSorting(sorting));
      dispatch(changeFlightsPage(0));
    }
  })
)(SortingBar);
