import React from "react";
import { getTime, formatDuration, getLabel } from "../../../../utils/functions";
require("./style.scss");

const getSegment = seg => (
  <div className="segment">
    <span className="time">{getTime(seg.time)}</span>
    <span className="day">{getTime(seg.time, "ddd D MMMM")}</span>
    <span className="airport">
      {seg.code} {seg.name}, {seg.country}
    </span>
  </div>
);

class SegmentDetails extends React.Component {
  render() {
    const {
      segment
      // cabinClass
    } = this.props;
    const {
      dep,
      arr,
      duration,
      flightNumber,
      airlineOperating,
      // hasWifi,
      aircraftType
    } = segment;
    return (
      <div className="segmentDetails">
        <div className="headline">
          {airlineOperating.name} - {getLabel("flight")} {airlineOperating.code}
          {flightNumber}
          {/*<span className="cabinClass"> // FIXME
            {" "}
            ({getLabel(`class.${cabinClass.code.toLowerCase()}`)})
            </span>*/}
          <span className="duration">{formatDuration(duration)}</span>
        </div>
        {getSegment(dep)}
        {getSegment(arr)}
        {aircraftType && <div className="airplane">{aircraftType}</div>}
      </div>
    );
  }
}

export default SegmentDetails;
