import config from "../../../config";
import {
  api,
  // getFlightsSearchUrl,
  getTime,
  getMatchedAirports,
  resetFlightsFilter,
  getLabel
} from "../../../utils/functions";

const { shops } = config;

export const refillSearchbox = async props => {
  const newSearchbox = {};
  const {
    // searchbox,
    match: { params },
    updateTravellers,
    updateCabinClass,
    updateIsOneway,
    updateAirportsNearby,
    updateDate,
    updateAirport
    // resetFlightsResults,
    // resetFlightsResponseCounter
  } = props;
  // resetFlightsResults()
  // resetFlightsResponseCounter()
  const {
    departureCode,
    destinationCode,
    travellers,
    departureDate,
    returnDate,
    departureIsNearby,
    destinationIsNearby
  } = params;
  const airports = await getMatchedAirports(departureCode, destinationCode);
  newSearchbox.departure = airports[0];
  updateAirport(
    "departure",
    newSearchbox.departure.value,
    newSearchbox.departure
  );
  newSearchbox.destination = airports[1];
  updateAirport(
    "destination",
    newSearchbox.destination.value,
    newSearchbox.destination
  );
  newSearchbox.departureDate = departureDate.replace(/-/g, "/");
  updateDate("departure", newSearchbox.departureDate);
  newSearchbox.returnDate =
    returnDate === "-" ? "" : returnDate.replace(/-/g, "/");
  updateDate("return", newSearchbox.returnDate);
  let [adt, ch, inf] = travellers.split("-");
  adt = +adt;
  ch = +ch;
  inf = +inf;
  if (
    !(
      adt >= 1 &&
      adt <= 8 &&
      ch >= 0 &&
      ch <= 8 &&
      inf >= 0 &&
      inf <= 8 &&
      inf <= adt
    )
  ) {
    // redirect("/"); // FIXME
    return;
  }
  newSearchbox.travellers = { adt, ch, inf };
  updateTravellers(newSearchbox.travellers);
  let { cabinClass } = params;
  cabinClass = cabinClass.toUpperCase();
  const cabinClassName = getLabel(`class.${cabinClass.toLowerCase()}`);
  if (!cabinClassName) {
    // redirect("/"); // FIXME
    return;
  }
  newSearchbox.cabinClass = { code: cabinClass, name: cabinClassName };
  updateCabinClass(newSearchbox.cabinClass);
  newSearchbox.isOneway = returnDate === "-";
  updateIsOneway(newSearchbox.isOneway);
  newSearchbox.departure.isNearby = departureIsNearby === "1";
  updateAirportsNearby("departure", newSearchbox.departure.isNearby);
  newSearchbox.destination.isNearby = destinationIsNearby === "1";
  updateAirportsNearby("destination", newSearchbox.destination.isNearby);
  return newSearchbox;
};

const onSearchCompleted = () => {};

const generateSegmentsKey = segments =>
  segments.map(segment => segment.flightNumber.toString().replace(/ /g, ""));

const generateFareKey = fare =>
  [
    ...generateSegmentsKey(fare.outbounds),
    ...generateSegmentsKey(fare.inbounds)
  ].join("-");

const onSearchResponse = (shop, response, props, isOneway) => {
  const {
    pushResults,
    increaseResponseCounter,
    updateFilterEdges,
    updateFilterValue,
    edges
  } = props;
  response.forEach(fare => {
    fare.key = generateFareKey(fare);
    fare.shops = [
      Object.assign({}, shop, { price: fare.price, url: fare.url })
    ];
    edges.price.min = Math.min(edges.price.min, Math.ceil(fare.price));
    edges.price.max = Math.max(edges.price.max, Math.ceil(fare.price));
    const duration = Math.max(
      fare.durations.outbound,
      fare.durations.inbound ? fare.durations.inbound : 0
    );
    edges.duration.min = Math.min(edges.duration.min, duration);
    edges.duration.max = Math.max(edges.duration.max, duration);
    let stops = fare.outbounds.length - 1;
    const depO = +getTime(fare.outbounds[0].dep.time, "X");
    const arrO = +getTime(
      fare.outbounds[fare.outbounds.length - 1].arr.time,
      "X"
    );
    if (depO < edges.departure.outbound.min)
      edges.departure.outbound.min = depO;
    if (depO > edges.departure.outbound.max)
      edges.departure.outbound.max = depO;
    if (arrO < edges.arrival.outbound.min) edges.arrival.outbound.min = arrO;
    if (arrO > edges.arrival.outbound.max) edges.arrival.outbound.max = arrO;
    if (!isOneway) {
      stops = Math.max(stops, fare.inbounds.length - 1);
      const depI = +getTime(fare.inbounds[0].dep.time, "X");
      const arrI = +getTime(
        fare.inbounds[fare.inbounds.length - 1].arr.time,
        "X"
      );
      if (depI < edges.departure.inbound.min)
        edges.departure.inbound.min = depI;
      if (depI > edges.departure.inbound.max)
        edges.departure.inbound.max = depI;
      if (arrI < edges.arrival.inbound.min) edges.arrival.inbound.min = arrI;
      if (arrI > edges.arrival.inbound.max) edges.arrival.inbound.max = arrI;
    }
    if (stops === 0) {
      edges.stops.zero = Math.min(edges.stops.zero, fare.price);
    } else if (stops === 1) {
      edges.stops.one = Math.min(edges.stops.one, fare.price);
    } else {
      edges.stops.more = Math.min(edges.stops.more, fare.price);
    }
    if (!edges.airlines[fare.marketingCarrier.code]) {
      edges.airlines[fare.marketingCarrier.code] = {
        price: fare.price,
        name: fare.marketingCarrier.name
      };
    } else {
      edges.airlines[fare.marketingCarrier.code].price = Math.min(
        edges.airlines[fare.marketingCarrier.code].price,
        fare.price
      );
    }
  });
  pushResults(response);
  increaseResponseCounter();
  updateFilterEdges(edges);
  resetFlightsFilter(updateFilterValue, edges, isOneway);
};

